.header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  box-shadow: 0px 6px 2px 0px rgba(0, 0, 0, 0.1);
  background-color: #eee8e8;
  z-index: 2;
  position: sticky;
  top: 0;
}

.app-text {
  font-size: 1.4rem;
  font-weight: 800;
}
